export default {
    props: ['modalOptions'],
    data() {
        return {
            openSimple: false
        }
    },
    computed: {
        modal() {
            var modal = this.modalOptions || {};
            var modalOptions = {
                title: modal.title || '系统提示',
                isCommitBtn: typeof modal.isCommitBtn === 'undefined' ? true : modal.isCommitBtn
            };
            return modalOptions;
        }
    },
    methods: {
        openSimpleDialog() {
            this.openSimple = true;
        },
        closeSimpleDialog() {
            this.openSimple = false;
        },
        commit() {
            this.$emit('commit');
        }
    }
}

